body {
  margin: 0;
  padding: 0;
  font-family: 'FFSubMono', monospace;
  background-color: rgb(15, 15, 15);
  color: rgb(255, 255, 255);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: rgb(17, 204, 17);
  text-decoration: none;
  transition: opacity 0.2s;
}

a:hover {
  opacity: 0.8;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1rem;
}

.header {
  text-align: center;
  margin: 3rem 0;
}

.title {
  font-size: 3rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.navigation {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin: 2rem 0;
  flex-wrap: wrap;
}

.navigation a {
  font-size: 1.2rem;
  padding: 0.5rem 0;
  position: relative;
}

.section {
  margin: 3rem 0;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.list-item {
  display: flex;
  margin-bottom: 0.5rem;
  align-items: baseline;
}

.list-item::before {
  content: "•";
  color: rgb(17, 204, 17);
  margin-right: 1rem;
}

.project-item {
  margin-bottom: 1rem;
}

.project-link {
  font-weight: bold;
}

.project-description {
  margin-left: 1.2rem;
  display: inline;
}

@media (max-width: 768px) {
  .navigation {
    gap: 1rem;
  }

  .title {
    font-size: 2.5rem;
  }
}