.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

/* Retro piksel tarzı stiller (aiswe.tech sitesine benzer) */
.title {
  font-family: 'FFSubMono', monospace;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.navigation {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin: 2rem 0;
  flex-wrap: wrap;
}

.navigation a {
  color: rgb(17, 204, 17);
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.2s;
  padding: 0.5rem 0;
}

.navigation a:hover {
  text-decoration: underline;
}

.section-title {
  font-size: 2rem;
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid rgba(17, 204, 17, 0.3);
  padding-bottom: 0.5rem;
}

.list-item {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
  line-height: 1.5;
}

.list-item::before {
  content: "•";
  color: rgb(17, 204, 17);
  position: absolute;
  left: 0;
}

.project-link {
  color: rgb(17, 204, 17);
  font-weight: bold;
  text-decoration: none;
}

.project-description {
  margin-left: 0.5rem;
  opacity: 0.9;
}

.about-content {
  margin-bottom: 2rem;
}

.contact-list {
  margin-top: 1rem;
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .title {
    font-size: 2.5rem;
  }

  .navigation {
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
  }
}